import React from 'react';
import { useEventLogger, useTranslator } from '../hooks';
import { Link } from '@mui/material';
import { FilesDirectoryLink } from './files-directory-link';
export function JobFileLink(props) {
    const trans = useTranslator('jupyterlab');
    if (!props.jobFile.file_path) {
        return null;
    }
    const log = useEventLogger();
    const fileBaseName = props.jobFile.file_path.split('/').pop();
    const title = props.jobFile.file_format === 'input'
        ? trans.__('Open input file "%1"', fileBaseName)
        : trans.__('Open output file "%1"', fileBaseName);
    if (props.jobFile.file_format === 'files') {
        return React.createElement(FilesDirectoryLink, { jobFile: props.jobFile, app: props.app });
    }
    return (React.createElement(Link, { key: props.jobFile.file_format, href: `/lab/tree/${props.jobFile.file_path}`, title: title, onClick: (e) => {
            e.preventDefault();
            if (props.parentComponentName) {
                log(`${props.parentComponentName}.${props.jobFile.file_format === 'input'
                    ? 'open-input-file'
                    : 'open-output-file'}`);
            }
            props.app.commands.execute('docmanager:open', {
                path: props.jobFile.file_path
            });
        }, style: { paddingRight: '1em' } }, props.children || props.jobFile.display_name));
}
