import React from 'react';
import cronstrue from 'cronstrue';
import { PathExt } from '@jupyterlab/coreutils';
import PauseIcon from '@mui/icons-material/Pause';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import { IconButton, Link } from '@mui/material';
import Stack from '@mui/material/Stack';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import { useEventLogger, useTranslator } from '../hooks';
import { ConfirmDeleteButton } from './confirm-buttons';
import { getErrorMessage } from '../util/errors';
function CreatedAt(props) {
    const create_date = props.job.create_time
        ? new Date(props.job.create_time)
        : null;
    const create_display_date = create_date
        ? create_date.toLocaleString()
        : null;
    return React.createElement(React.Fragment, null, create_display_date);
}
function ScheduleSummary(props) {
    if (props.schedule === undefined) {
        return null;
    }
    return React.createElement(React.Fragment, null, cronstrue.toString(props.schedule));
}
function PauseButton(props) {
    const trans = useTranslator('jupyterlab');
    if (!props.jobDef.active) {
        return null;
    }
    const buttonTitle = props.jobDef.name
        ? trans.__('Pause "%1"', props.jobDef.name)
        : trans.__('Pause job definition');
    return (React.createElement(IconButton, { onClick: props.clickHandler, title: buttonTitle },
        React.createElement(PauseIcon, { fontSize: "small" })));
}
function ResumeButton(props) {
    const trans = useTranslator('jupyterlab');
    if (props.jobDef.active) {
        return null;
    }
    const buttonTitle = props.jobDef.name
        ? trans.__('Resume "%1"', props.jobDef.name)
        : trans.__('Resume job definition');
    return (React.createElement(IconButton, { onClick: props.clickHandler, title: buttonTitle },
        React.createElement(PlayArrowIcon, { fontSize: "small" })));
}
export function buildJobDefinitionRow(jobDef, app, openJobDefinitionDetail, deleteRow, forceReload, trans, ss, handleApiError) {
    const log = useEventLogger();
    const cellContents = [
        // name
        React.createElement(Link, { onClick: () => {
                log('job-definition-list.open-detail');
                openJobDefinitionDetail(jobDef.job_definition_id);
            }, title: `Open detail view for "${jobDef.name}"` }, jobDef.name),
        PathExt.basename(jobDef.input_filename),
        React.createElement(CreatedAt, { job: jobDef }),
        React.createElement(ScheduleSummary, { schedule: jobDef.schedule }),
        jobDef.active ? trans.__('Active') : trans.__('Paused'),
        React.createElement(Stack, { spacing: 1, direction: "row" },
            React.createElement(PauseButton, { jobDef: jobDef, clickHandler: async () => {
                    log('job-definition-list.pause');
                    handleApiError(null);
                    ss.pauseJobDefinition(jobDef.job_definition_id)
                        .then(_ => {
                        forceReload();
                    })
                        .catch((e) => {
                        const message = getErrorMessage(e);
                        handleApiError(message);
                    });
                } }),
            React.createElement(ResumeButton, { jobDef: jobDef, clickHandler: async () => {
                    log('job-definition-list.resume');
                    handleApiError(null);
                    ss.resumeJobDefinition(jobDef.job_definition_id)
                        .then(_ => {
                        forceReload();
                    })
                        .catch((e) => {
                        const message = getErrorMessage(e);
                        handleApiError(message);
                    });
                } }),
            React.createElement(ConfirmDeleteButton, { name: jobDef.name, clickHandler: async () => {
                    log('job-definition-list.delete');
                    handleApiError(null);
                    ss.deleteJobDefinition(jobDef.job_definition_id)
                        .then(_ => {
                        deleteRow(jobDef.job_definition_id);
                    })
                        .catch((e) => {
                        const message = getErrorMessage(e);
                        handleApiError(message);
                    });
                } }))
    ];
    return (React.createElement(TableRow, { key: jobDef.job_definition_id }, cellContents.map((cellContent, idx) => (React.createElement(TableCell, { key: `${jobDef.job_definition_id}-${idx}` }, cellContent)))));
}
